import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { CalculatorService } from './services/calculator.service';
import { CookieService } from '../../shared/services/index';
import { environment } from '../../../environments/environment';
import { Script } from '../../shared/services/script.service';

declare var ga: Function;
declare var jQuery: any;
declare var window: any;

@Component({
  selector: 'og-calc',
  templateUrl: './calculator.component.html',
  styleUrls: ['./calculator.component.css'],
})

export class CalculatorComponent implements OnInit {

  tempName: String;
  pageStatus: String;
  company: String;
  JSON_Template: any;
  link: string;
  isLoggedin: Boolean = false;
  subdomain: string;

  constructor(private _calService: CalculatorService,
    private _cookieService: CookieService,
    private _router: Router,
    private _script: Script,
    private titleService: Title
  ) { }

  ngOnInit() {
    let url = window.location.href;
    let urlParts = url.split('/');
    //calc name
    let name = urlParts[3].split('?')[0];
    //subdomian
    this.subdomain = urlParts[2].split('.')[0];

    if (name === 'himanshu' || name.charAt(0) === '?') {
      this._router.navigate(['/']);
    }
    else {
      //load scripts
      this._script.load('selectize', 'rangeSlider', 'math', 'iFrameResizer')
        .then((data) => {
          console.log('Scripts', data);
        })
        .catch((error) => {
          //any error
        });

      //get App and initialize page
      this.initPage(name);
    }
  }

  initPage(name: string) {
    this._calService.getCompanyApp(name, this.subdomain)
      .subscribe(
      (response: any) => {
        if (response != 'CALC_NOT_FOUND') {
          // if (this.loggedIn()) {
          //   response.status = 'DEV';
          //   localStorage.setItem('template', JSON.stringify(response));
          //   this._router.navigate(['/preview']);
          // } else {
            if (response.mode != 'PRIVATE') {
              this.titleService.setTitle(response.title);
              jQuery('meta[name=description]').attr('content', response.description);
              jQuery('#favicon').attr('href', response.favicon);
              this.JSON_Template = response;
              this.tempName = response.template;
              //handle tracking
              this.googleAnalytics(response);
            } else
              this.pageStatus = 'Private';
          //}
        }
        else {
          let storage: any = this._cookieService.readCookie('storage');
          if (storage) this.isLoggedin = true;
          this.pageStatus = 'Not-Found';
        }
      },
      (error: any) => {
        console.log(error);
      }
      );
  }

  loggedIn(): boolean {
    let storage: any = JSON.parse(this._cookieService.readCookie('storage'));
    // console.log("@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@",storage.user);
    if (this._cookieService.readCookie('storage') && storage.user.role !== "ADMIN" && storage.companyList.includes(this.subdomain))
      return true;
    else
      return false;
  }

  googleAnalytics(calc: any): void {
    //ours
    ga('devteam.send', 'pageview', '/' + calc.parentApp);
    ga('devteam.send', 'pageview', '/' + calc.company);
    ga('devteam.send', 'event', calc.name + ' Page View', 'Calculator Visited', calc.url);

    //users
    if (calc.ga) {
      ga('create', calc.ga, 'auto', 'userCustom');
      ga('userCustom.send', 'pageview', '/' + calc.url);
      ga('userCustom.send', 'event', calc.name + ' Page View', 'Calculator Visited', calc.url);
    }
  }

  pricing() {
    this.link = environment.APP_EXTENSION;
    let protocol = environment.PROTOCOL;
    let url = this.link + '/pricing.html';
    jQuery(location).attr('href', protocol + url);

  }

  features() {
    this.link = environment.APP_EXTENSION;
    let protocol = environment.PROTOCOL;
    let url = this.link + '/features.html';
    jQuery(location).attr('href', protocol + url);

  }

  whyCalculators() {
    this.link = environment.APP_EXTENSION;
    let protocol = environment.PROTOCOL;
    let url = this.link + '/why_calculators.html';
    jQuery(location).attr('href', protocol + url);

  }

  examples() {
    this.link = environment.APP_EXTENSION;
    let protocol = environment.PROTOCOL;
    let url = this.link + '/examples.html';
    jQuery(location).attr('href', protocol + url);

  }

  login() {
    this._router.navigate(['/login']);
  }

  dashboard() {
    this._router.navigate(['/dashboard']);
  }
}



